<template>
    <div v-if="type == 'edit'">
        <!-- 編輯區塊 -->
        <!-- batch 梯次選項 -->
        <small
            class="form-text text-muted form-group"
            v-if="content.des && content.des[i18n]"
        >{{ content.des[i18n] }}</small>
        <div v-for="(data, idx) in content.datas" :key="idx" class="form-group">
            <label>{{ data.label[i18n] }}</label>
            <div>
                <div
                    v-for="(option, idx2) in data.options"
                    class="custom-control form-check"
                    :class="{
                        'form-inline': !content.setting.inline,
                        'custom-control-inline': content.setting.inline,
                    }"
                    :key="idx2"
                >
                    <input
                        type="radio"
                        :id="`batch_${idx}_${idx2}`"
                        :value="idx2"
                        class="form-check-input"
                    />
                    <label class="form-check-label">{{ option.label[i18n] }}</label>
                </div>
            </div>
        </div>

        <!-- batch end -->
    </div>
    <div v-else>
        <!-- 欄位設定 -->
        <displayInline :currentEdit="currentEdit"></displayInline>
        <!-- 梯次選項 -->
        <button
            data-toggle="tooltip"
            type="button"
            class="ml-0 btn btn-default btn-sm waves-effect waves-light"
            @click="addGroup"
        >新增類別</button>
        <div class="form-group" v-for="(data, index) in currentEdit.datas" :key="index">
            
            <table class="table">
                <thead class="grey lighten-3">
                    <tr>
                        <th colspan="3">
                            <div v-for="lang in enumData.langs" :key="`類別-${index}-${lang.key}`">
                                <label :for="`類別-${index}-${lang.key}`">類別 - {{lang.label}}</label>
                                <input
                                    :id="`類別-${index}-${lang.key}`"
                                    v-model="data.label[lang.key]"
                                    class="form-control form-control-sm"
                                />
                            </div>
                            <button
                                data-toggle="tooltip"
                                type="button"
                                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                                @click="addBatch(index)"
                            >新增梯次</button>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(option, optionsIndex) in data.options" :key="optionsIndex">
                        <td class="text-center">{{ optionsIndex + 1 }}</td>
                        <td class="text-center">
                            <div class="btn-group">
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    :disabled="optionsIndex == 0"
                                    @click="$upOption(data.options, optionsIndex)"
                                >
                                    <i class="fa fa-chevron-up"></i>
                                </button>
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    :disabled="optionsIndex == data.options.length - 1"
                                    @click="$downOption(data.options, optionsIndex)"
                                >
                                    <i class="fa fa-chevron-down"></i>
                                </button>
                                <button
                                    title
                                    data-toggle="tooltip"
                                    type="button"
                                    class="btn btn-xs btn-default waves-effect waves-light"
                                    @click="$removeOption(data.options, optionsIndex)"
                                >
                                    <i class="fa fa-times"></i>
                                </button>
                            </div>
                        </td>
                        <td>
                            <div v-for="lang in enumData.langs" :key="`選項內容-${optionsIndex}-${index}-${lang.key}`">
                                <label :for="`選項內容-${optionsIndex}-${index}-${lang.key}`">{{lang.label}}</label>
                                <input
                                    :id="`選項內容-${optionsIndex}-${index}-${lang.key}`"
                                    v-model="option.label[lang.key]"
                                    class="form-control form-control-sm"
                                />
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- batch modal -->
        <div class="modal fade" id="batchModal">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">報名梯次設定</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                            <div v-for="lang in enumData.langs" :key="`梯次名稱${lang.key}`">
                                <label :for="`梯次名稱${lang.key}`">梯次名稱 - {{lang.label}}</label>
                                <input
                                    :id="`梯次名稱${lang.key}`"
                                    v-model="batchEdit.label[lang.key]"
                                    placeholder="請輸入梯次名稱"
                                    class="form-control col-md-12"
                                />
                            </div>
                        </div>
                       
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm btn-default waves-effect waves-light"
                            @click="insertBatch()"
                        >儲存</button>
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="closeBatch"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>
         <!-- group modal -->
        <div class="modal fade" id="groupModal">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">報名梯次設定</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                             <div v-for="lang in enumData.langs" :key="`類別名稱${lang.key}`">
                                <label :for="`類別名稱${lang.key}`">類別名稱 - {{lang.label}}</label>
                                <input
                                    :id="`類別名稱${lang.key}`"
                                    v-model="groupEdit.label[lang.key]"
                                    placeholder="請輸入類別名稱"
                                    class="form-control col-md-12"
                                />
                            </div>
                        </div>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm btn-default waves-effect waves-light"
                            @click="insertGroup"
                        >儲存</button>
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="closeGroup"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import displayInline from "@/components/DisplayInline";

export default {
    name: "batch",
    components: {
        displayInline
    },
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        },
        enumData: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {
            // 梯次
            batchEdit: {
                label: {},
            },
            batchIndex: '',
            // 類別
            groupEdit: {
                label: {},
                options: []
            },
        };
    },
    created() {},
    methods: {
        addGroup(){
            $("#groupModal")
                .appendTo("body")
                .modal("show");
        },
        insertGroup() {
            this.currentEdit.datas.push(this.groupEdit);
            this.closeGroup();
        },
        closeGroup() {
            this.groupEdit = {
                label: {},
                options: []
            }
            $("#groupModal").modal("hide");
        },
        addBatch(index) {
            this.batchIndex = index;
            $("#batchModal")
                .appendTo("body")
                .modal("show");
        },
        insertBatch() {
            this.currentEdit.datas[this.batchIndex].options.push(
                { 
                    label: this.batchEdit.label,
                    uuid: this.$uuid() 
                }
            );
            this.closeBatch();
        },
        closeBatch() {
            this.batchEdit = {
                label: {},
            };
            this.batchIndex = "";
            $("#batchModal").modal("hide");
        }
    }
};
</script>
