<template>
    <div v-if="type == 'edit'">
        <!-- 編輯區塊 -->
        <div class="form-row">
            <div :class="content.setting.width || 'col'">
                <template v-for="(option, optionIdx) in content.options">
                    <div
                        class="form-check form-inline"
                        :key="optionIdx"
                        :class="{
                            'form-inline': !content.setting.inline,
                            'custom-control-inline': content.setting.inline,
                            'mb-2': option.des || optionIdx == Object.keys(content.options).length - 1,
                        }"
                    >
                        <input
                            type="radio"
                            :id="`first_review_status_${optionIdx}`"
                            name="first_review_status"
                            :value="option.value"
                            class="form-check-input"
                        />
                        <label
                            class="form-check-label"
                            :for="`first_review_status_${optionIdx}`"
                        >{{ option.label[i18n] }}</label>
                        <input
                            type="text"
                            class="form-control form-control-sm ml-2"
                            v-if="option.des"
                            :placeholder="option.placeholder[i18n]"
                        />
                    </div>
                    <div
                        v-if="option.value == 1"
                        :key="optionIdx + '_note'"
                        class="mb-2"
                        style="padding-left: 1.25rem;"
                    >
                        <textarea
                            name="first_review_replacement_note"
                            class="form-control"
                            rows="2"
                            placeholder="補件說明"
                        ></textarea>
                    </div>
                </template>
                <div class="mb-2" style="padding-left: 1.25rem;">
                    <label>審查備註</label>
                    <textarea
                        name="first_review_note"
                        class="form-control"
                        rows="2"
                        placeholder="審查備註"
                    ></textarea>
                </div>
            </div>
        </div>
    </div>
    <div v-else>
        <!-- 欄位設定 -->
        <displayInline :currentEdit="currentEdit"></displayInline>
        <tableOptions :currentEdit="currentEdit" :langs="enumData.langs"></tableOptions>
    </div>
</template>

<script>
import displayInline from "@/components/DisplayInline";
import tableOptions from "@/components/TableOptions";

export default {
    name: "first_review_status",
    components: {
        displayInline,
        tableOptions
    },
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        },
        enumData: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {};
    },
    computed: {
        firstReviewStatus() {
            return this.enumData.firstReviewStatus.map((item, idx) => {
                let des = false;
                let placeholder = { CH: "", EN: "" };

                // 通過
                if (idx == 3) { // APPLY_FIRST_REVIEW_FINISH_PASS
                    des = true;
                    placeholder = { CH: "排名", EN: "Rank" };
                }

                return {
                    value: idx,
                    label: item,
                    des: des,
                    placeholder: placeholder
                };
            });
        }
    },
    created() {
        if (this.currentEdit && typeof this.currentEdit.options !== 'undefined') {
            // optinos 沒有值 從enumData 拿預設
            this.currentEdit.options = this.firstReviewStatus;
        }
    }
};
</script>
