<template>
    <div v-if="type == 'edit'">
        <!-- 編輯區塊 -->
        <!-- wish 學期志願選項 -->
        <small
            class="form-text text-muted form-group"
            v-if="content.des && content.des[i18n]"
        >{{ content.des[i18n] }}</small>
        <div class="form-group row">
            <div class="col-3">
                <select class="browser-default custom-select">
                    <option
                        v-for="area in areaList"
                        :key="area.id"
                        :value="area.id"
                    >{{ area.name[i18n] }}</option>
                </select>
            </div>
            <div class="col-9">
                <select class="browser-default custom-select" v-model="selectedWish">
                    <option
                        v-for="(wish, wishIndex) in content.wish"
                        :key="wishIndex"
                        :value="wishIndex"
                        :disabled="wishIsSeleted(wishIndex)"
                    >{{ wish.name[i18n] }}</option>
                </select>
            </div>
            <div class="col-12">
                <button type="button" class="btn btn-default btn-sm ml-0 waves-effect waves-light" @click="addWishItem()" :disabled="addWishList.length >= content.setting.limit">加入志願</button>
            </div>
        </div>
        <div class="form-group">
            <ol>
                <template v-for="(addWish, addIndex) in addWishList">
                    <li :key="'wishOption_' + addIndex">
                        {{areaList[addWish.areaId]}} {{addWish.name[i18n]}}
                        <button type="button" class="close float-none align-bottom" aria-label="Close" @click="delWishItem(addIndex)">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </li>
                </template>
            </ol>
        </div>
    </div>
    <div v-else>
        <!-- 欄位設定 -->
        <!-- wish 志願選項 -->
        <div class="form-group">
            <label for="wish_limit">開放填寫志願數</label>
            <input
                class="form-control form-control-sm"
                type="number"
                v-model="currentEdit.setting.limit"
                id="wish_limit"
                min="0"
            />
        </div>
        <button
            data-toggle="tooltip"
            type="button"
            class="pull-right ml-0 btn btn-default btn-sm waves-effect waves-light"
            @click="$editModal('wish', wishInit, 'new')"
        >新增志願</button>
        <div class="form-group">
            <label>志願地區</label>
            <div class="table-responsive">
                <table class="table">
                    <thead class="grey lighten-3">
                        <tr>
                            <th style="min-width: 50px; width: 50px;" class="text-center">#</th>
                            <th style="min-width: 110px; width: 110px;" class="text-center">操作</th>
                            <th class="text-center">區域</th>
                            <th class="text-center" v-for="lang in enumData.langs" :key="lang.key">駐外單位 - {{lang.label}}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(data, index) in currentEdit.wish" :key="index">
                            <td class="text-center">{{ index + 1 }}</td>
                            <td class="text-center">
                                <div class="btn-group">
                                    <button
                                        title
                                        data-toggle="tooltip"
                                        type="button"
                                        class="btn btn-xs btn-default waves-effect waves-light"
                                        @click="$editModal('wish', wishInit, index)"
                                    >
                                        <i class="fa fa-pencil"></i>
                                    </button>

                                    <button
                                        title
                                        data-toggle="tooltip"
                                        type="button"
                                        class="btn btn-xs btn-default waves-effect waves-light"
                                        @click="$removeOption(currentEdit.wish, index)"
                                    >
                                        <i class="fa fa-times"></i>
                                    </button>
                                </div>
                            </td>
                            <td
                                class="text-center"
                            >{{ enumData.areas[data.areaId]['CH'] }}</td>
                            <td class="text-center" v-for="lang in enumData.langs" :key="lang.key">{{ data.name[lang.key] }}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <!-- wish modal -->
        <div class="modal fade" id="wishModal">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">志願地點</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <div class="form-group">
                            <label for="wishArea">區域</label>
                            <select
                                class="browser-default custom-select"
                                v-model="wishEdit.areaId"
                                id="wishArea"
                            >
                                <option
                                    v-for="area in areaList"
                                    :key="area.id"
                                    :value="area.id"
                                >{{ area.name['CH'] }}</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <div v-for="lang in enumData.langs" :key="`地點${lang.key}`">
                                <label :for="`地點${lang.key}`">地點 - {{lang.label}}</label>
                                <input
                                    :id="`地點${lang.key}`"
                                    v-model="wishEdit.name[lang.key]"
                                    placeholder="請輸入地點"
                                    class="form-control col-md-12"
                                />
                            </div>
                            <!-- <label for="wishName">地點</label>
                            <input
                                v-model="wishEdit.name"
                                id="wishName"
                                placeholder="請輸入地點"
                                class="form-control col-md-12"
                            /> -->
                        </div>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button
                            type="button"
                            class="btn btn-sm btn-default waves-effect waves-light"
                            @click="$saveModal('wish')"
                        >儲存</button>
                        <button
                            type="button"
                            class="btn btn-sm waves-effect waves-light"
                            @click="$closeModal('wish', wishInit)"
                        >關閉</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- wish end -->
    </div>
</template>

<script>
export default {
    name: "wish",
    props: {
        type: {
            type: String,
            required: true,
            default: ""
        },
        i18n: {
            type: String,
            required: true,
            default: ""
        },
        content: {
            type: Object,
            default: function() {
                return {};
            }
        },
        currentEdit: {
            type: Object,
            default: function() {
                return {};
            }
        },
        enumData: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    
    data() {
        return {
            // 已選志願
            selectedWish: '',
            // 已加入志願
            addWishList: [],
            // 志願選項
            wishEdit: {
                id: "",
                name: {},
                areaId: ""
            },
            wishInit: {
                name: {},
                areaId: ""
            },
            wishEditIndex: ""
        };
    },
    computed: {
        areaList: function() {
            if(this.enumData.areas){
                return Object.entries(this.enumData.areas).map(([key, value]) => {
                    return {
                        id: key,
                        name: value
                    }
                });
            }else{
                return []
            }
        }
    },
    created() {},
    methods: {
        // 加入志願
        addWishItem() {
            let selected = this.selectedWish;

            if (selected !== '' && this.addWishList.length < this.content.setting.limit) {
                let target = this.content.wish[selected];

                if (typeof target !== 'undefined') {
                    target['id'] = selected;
                    this.addWishList.push(target);
                }
            }
        },
        // 判斷志願是否加入
        wishIsSeleted(wishIndex) {
            return this.addWishList.some(function (item) {
                return item.id == wishIndex;
            });
        },
        // 刪除加入的志願
        delWishItem(addWishListIndex) {
            this.addWishList.splice(addWishListIndex, 1);
        }
    }
};
</script>
