<template>
    <div>
        <label>選項</label>
        <div class="form-group">
            <div class="table-responsive">
                <table class="table">
                    <thead class="grey lighten-3">
                        <tr>
                            <th style="min-width: 120px; width: 120px;" class="text-center">索引</th>
                            <th class="text-center">選項文字</th>
                            <th
                                class="text-center"
                                v-if="
                                    currentEdit.type == 'radio' ||
                                    currentEdit.type == 'checkbox' ||
                                    currentEdit.type == 'gender' ||
                                    currentEdit.type == 'native_type' ||
                                    currentEdit.type == 'first_review_status' ||
                                    currentEdit.type == 'result_review_status' ||
                                    currentEdit.type == 'result_review_status_semester' ||
                                    currentEdit.type == 'result_review_status_wish' ||
                                    currentEdit.type == 'diet' ||
                                    currentEdit.type == 'accommodation' ||
                                    currentEdit.type == 'check_in_status' ||
                                    currentEdit.type == 'join_status' ||
                                    currentEdit.type == 'is_vip' ||
                                    currentEdit.type == 'guardian_relationship' ||
                                    currentEdit.type == 'with_dependent_key' ||
                                    currentEdit.type == 'dependent_gender' ||
                                    currentEdit.type == 'dependent_relationship' ||
                                    currentEdit.type == 'dependent_diet' ||
                                    currentEdit.type == 'airport_pick_up_key' ||
                                    currentEdit.type == 'airport_drop_off_key'
                                "
                            >額外輸入</th>
                            <th class="text-center"></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(option, index) in currentEdit.options" :key="index">
                            <td class="text-center">
                                <template v-if="
                                    currentEdit.type == 'first_review_status' ||
                                    currentEdit.type == 'result_review_status' ||
                                    currentEdit.type == 'result_review_status_semester' ||
                                    currentEdit.type == 'result_review_status_wish'
                                ">
                                    {{option.value}}
                                </template>
                                <input
                                    v-else
                                    v-model="option.value"
                                    class="form-control form-control-sm"
                                    @change="editOptionValue"
                                />
                            </td>
                            <td>
                                <div v-for="lang in langs" :key="`選項內容${index}-${lang.key}`">
                                    <label :for="`選項內容${index}-${lang.key}`">{{lang.label}}</label>
                                    <input
                                        :id="`選項內容${index}-${lang.key}`"
                                        v-model="option.label[lang.key]"
                                        class="form-control form-control-sm"
                                    />
                                </div>
                            </td>
                            <td
                                v-if="
                                    currentEdit.type == 'radio' ||
                                    currentEdit.type == 'checkbox' ||
                                    currentEdit.type == 'gender' ||
                                    currentEdit.type == 'native_type' ||
                                    currentEdit.type == 'first_review_status' ||
                                    currentEdit.type == 'result_review_status' ||
                                    currentEdit.type == 'result_review_status_semester' ||
                                    currentEdit.type == 'result_review_status_wish' ||
                                    currentEdit.type == 'diet' ||
                                    currentEdit.type == 'accommodation' ||
                                    currentEdit.type == 'check_in_status' ||
                                    currentEdit.type == 'join_status' ||
                                    currentEdit.type == 'is_vip' ||
                                    currentEdit.type == 'guardian_relationship' ||
                                    currentEdit.type == 'with_dependent_key' ||
                                    currentEdit.type == 'dependent_gender' ||
                                    currentEdit.type == 'dependent_relationship' ||
                                    currentEdit.type == 'dependent_diet' ||
                                    currentEdit.type == 'airport_pick_up_key' ||
                                    currentEdit.type == 'airport_drop_off_key'
                                "
                            >
                                <div class="custom-control form-check form-inline pl-0">
                                    <input
                                        :id="`options_des_${index}`"
                                        class="form-check-input"
                                        type="checkbox"
                                        v-model="option.des"
                                        @change="checkInline"
                                    />
                                    <label class="form-check-label" :for="`options_des_${index}`"></label>
                                </div>
                                <template v-if="option.des == true">
                                    <div v-for="lang in langs" :key="`欄位提示${index}-${lang.key}`">
                                        <label :for="`欄位提示${index}-${lang.key}`">{{lang.label}}</label>
                                        <input
                                            :id="`欄位提示${index}-${lang.key}`"
                                            v-model="option.placeholder[lang.key]"
                                            class="form-control form-control-sm"
                                        />
                                    </div>
                                </template>
                            </td>
                            <td class="text-center">
                                <div class="btn-group">
                                    <button
                                        title
                                        data-toggle="tooltip"
                                        type="button"
                                        class="btn btn-xs btn-default waves-effect waves-light"
                                        :disabled="index == 0"
                                        @click="$upOption(currentEdit.options, index)"
                                    >
                                        <i class="fa fa-chevron-up"></i>
                                    </button>
                                    <button
                                        title
                                        data-toggle="tooltip"
                                        type="button"
                                        class="btn btn-xs btn-default waves-effect waves-light"
                                        :disabled="
                                            index == currentEdit.options.length - 1
                                        "
                                        @click="$downOption(currentEdit.options, index)"
                                    >
                                        <i class="fa fa-chevron-down"></i>
                                    </button>
                                    <button
                                        v-if="
                                            currentEdit.type != 'first_review_status' &&
                                            currentEdit.type != 'result_review_status' &&
                                            currentEdit.type != 'result_review_status_semester' &&
                                            currentEdit.type != 'result_review_status_wish'
                                        "
                                        title
                                        data-toggle="tooltip"
                                        type="button"
                                        class="btn btn-xs btn-default waves-effect waves-light"
                                        @click="$removeOption(currentEdit.options, index)"
                                    >
                                        <i class="fa fa-times"></i>
                                    </button>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <button
                v-if="
                    currentEdit.type != 'first_review_status' &&
                    currentEdit.type != 'result_review_status' &&
                    currentEdit.type != 'result_review_status_semester' &&
                    currentEdit.type != 'result_review_status_wish'
                "
                data-toggle="tooltip"
                type="button"
                class="ml-0 btn btn-default btn-sm waves-effect waves-light"
                @click="addOption"
            >新增項目</button>
        </div>
    </div>
</template>

<script>
export default {
    name: "table_options",
    props: ["currentEdit", "langs"],
    mounted: function() {},
    methods: {
        addOption() {
            this.currentEdit.options.push({ value: "", label: {} });
        },
        editOptionValue() {
            try {
                this.checkValue(this.currentEdit.options);
            } catch (error) {
                toastr["error"]("Error", error.message);
                return;
            }
        },
        checkValue(arr) {
            // 檢查 options value 是否重複
            if (arr && arr.length > 0) {
                if (
                    new Set(arr.map(item => item.value.toString())).size <
                    arr.length
                ) {
                    // 重複
                    throw new Error("索引不可重複");
                }
            }
        },
        checkInline() {
            let des = this.currentEdit.options.some(item => {
                if (item.des) {
                    this.currentEdit.setting.inline = false;
                    return true;
                }
            });
            if (!des && this.currentEdit.setting.inline) {
                this.currentEdit.setting.inline = true;
            }
        }
    }
};
</script>
